*{
    padding: 0;
    margin: 0;
    nav-down: auto;
}
.main{
    width: 100%;
    background-image:url(./icons/background.jpg);
}
.nav{
    width: 100%;
    height: 100px;
    background-color: rgba(88, 1, 112, 0.5);
    border-bottom: 3px solid white;
    display: flex;
}
.logo{
    width: 50%;
    height: 100px;
    display: flex;
}
#logo1{
    height: 80px;
    margin-left: 30px;
}
#name{
    color: white;
    padding-top:30px;
    margin-left: 20px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 30px;
}
.links{
    margin-left: 300px;
    display: flex;

}
.links div{
    margin: 10px;
    width: 150px;
    height: 40px;
    text-align: center;
    justify-content: center;
    align-self: center;
    align-content: center;
    border-radius: 10px;
}
#link{
    color: white;
    font-size: 20px;

}
#link:hover{
    border-radius: 10px;
    transition: 1000ms;
    font-size: 25px;
    color: black;
    background-color: rgba(29, 130, 225, 0.173);
    
}
.central-part{
    padding-left: 100px;
    padding-top: 100px;
    display: flex;
}
#top-text{
    font-size: 50px;
    font-weight: 900;
    color: lightpink;
}
.text{
    width: 50%;
    height: 500px;
}
#bottom-text{
    color: white;
}

.right-side {
    position: relative;
    width: 50%;
    border-radius: 50px;
    overflow: hidden;
  }
  
  .right-side img {
    margin-left: 170px;
    object-fit: cover;
    width: 50%;
    height: auto;
    border-radius: 20%;
    transition: transform 0.8s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .right-side:hover img {
    transform: rotateY(180deg);
    filter: blur(5px);
  }
  
  .hover-text {
    position: absolute;
    width: 50%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: white;
    opacity: 0; /* Initially hidden */
    transition: opacity 0.5s ease;
  }
  
  .right-side:hover .hover-text {
    opacity: 1; /* Show text on hover */
  }
  
.bota{
    display: flex;
    width: 100%;
    height: 100px;
    background-color: rgba(72, 8, 157, 0.5);
    justify-content: center;
}
.fa-brands{
    color: white;
    margin-top: 30px;
    margin-left: 50px;
}
.ins{
    font-size: 50px;
}